@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,100&display=swap");@import url("https://fonts.googleapis.com/css2?family=Average+Sans&display=swap");@import url("https://fonts.googleapis.com/css2?family=Hind+Madurai&display=swap");*{margin:0;padding:0;overflow:hidden}*,*::after,*::before{box-sizing:border-box}body{margin:0;font-family:"Hind Madurai", sans-serif;font-size:16px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:white}h1{font-family:"Montserrat", sans-serif;color:white}h2{font-family:"Average Sans", sans-serif;color:white}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.background{background:linear-gradient(225deg, #2e2d40 0%, #3c3a61 100%);min-height:100vh;display:flex}.circle1,.circle2{height:calc(max(50vw, 50vh));width:calc(max(50vw, 50vh));position:fixed;border-radius:50%;filter:blur(40px)}.circle1{background:#3f3e5c;background:linear-gradient(280deg, #3f3e5c 0%, #3a3c4a 100%);top:-10vw;left:-10vw}.circle2{background:#34334a;background:linear-gradient(253deg, #34334a 0%, #2c2e3d 100%);bottom:-15vw;right:-5vw}.main-content{display:flex;position:relative;text-align:center;z-index:1;width:100%;height:100vh}.button-container{height:75px;width:fit-content;background:#135ef2;background:linear-gradient(270deg, #135ef2 0%, #5295ed 100%);box-shadow:4px 4px 5px rgba(0,0,0,0.25);border-radius:999px;padding:0px 20px;margin:20px 0px;display:flex;justify-content:center;align-items:center;font-weight:300;font-size:2rem}.button-container:hover{cursor:pointer}.button-text{width:fit-content;margin:0 30px}.button-link{text-decoration:none;color:white}.icon-button-container{height:75px;width:75px;background:#135ef2;background:linear-gradient(270deg, #135ef2 0%, #5295ed 100%);border-radius:50%;margin:20px 0px;display:flex;justify-content:center;align-items:center}.icon-button-container:hover{cursor:pointer}@keyframes fadein{0%{opacity:0%}100%{opacity:100%}}.navbar{min-